<template>
    <view>
        <view style="position: fixed;
    width: 100%;
    top: 0;
    left: 0;
    z-index: 99;
    " id="navBar">
            <kt-nav-bar title="OVO情趣店铺"></kt-nav-bar>
        </view>

        <pageExchange v-show="pageComponent == 'pageExchange'"
        :navBarHeight="navBarHeight"
        ></pageExchange>

        <pageMyInfo v-show="pageComponent == 'pageMyInfo'"
        :navBarHeight="navBarHeight"
        ></pageMyInfo>


        <tabbar :pageComponent="pageComponent" @tabbarClick="tabbarClick"></tabbar>
    </view>
</template>

<script>
import tabbar from './tabbar/tabbar.vue';
import pageExchange from './switch/pageExchange.vue';
import pageMyInfo from './switch/pageMyInfo.vue';

export default {
    components: {
        tabbar,
        pageExchange,
        pageMyInfo,
    },
    data() {
        return {
            pageComponent: 'pageExchange',
            userSelf: {},
            navBarHeight: 0,
        };
    },
    created() {
        this.userSelf = this.$ovo.getSelf();
    },
    mounted() {
        // 获取navBar高度
        this.$nextTick(() => {
            uni.createSelectorQuery().select('#navBar').boundingClientRect((rect) => {
                this.navBarHeight = rect.height;
            }).exec();
        });
    },
    methods: {
        tabbarClick(pageComponent) {
            this.pageComponent = pageComponent;
        }
    }

}
</script>

<style lang="scss" scoped></style>
